/*  Our story page hero banner
*
*  Version:
*    1.0.0 - 2020/11/13
*/
@import '../base.scss';

.c-our-story-hero {

  &__header {
    background-color: $s-color--background-tertiary;
    margin-bottom: 12rem;

    &-inner {
      padding-right: 2.5rem;
      padding-top: t-to-rem(128);
      padding-bottom: t-to-rem(46);
    }

    &-heading {
      color: $s-color--font-6;
      margin-bottom: 1.75rem;
      position: relative;

      .is-dark-mode & {
        color: $s-color--font-1;
      }


      strong {
        font-weight: inherit;
        color: $s-color--dark-blue;
      }

      &:before {
        content: 'inspiration';
        color: $s-color--dark-blue;
        position: absolute;
        left: -88%;
        opacity: .03;
        bottom: 0;
      }

      &:after {
        content: 'inspiration';
        color: $s-color--dark-blue;
        position: absolute;
        right: -35%;
        opacity: .03;
        bottom: 0;
      }
    }
  }

  &__usps {
    display: none;
    align-items: center;
    padding-top: 6rem;
    max-width: 50%;

    svg {
      width: 3.5625rem;
      height: auto;
    }

    &-wrap {
      background-color: $s-color--background
    }
  }

  &__usp {
    display: flex;
    align-items: center;

    &-title {
      padding: 0 2rem 0 1rem;
    }
  }

  @include t-media-query($s-small-down) {

    &__header {
      &-inner {
        max-width: 25rem;
        margin: 0 auto;
      }

      &-left-container {
        padding-left: 5.5rem;
      }
    }

    &__pre-title {
      margin-bottom: 1.3125rem;
    }

    &__text {
      margin-bottom: t-to-rem(72);
      text-align: center;
    }

    &__image {
      &.is-desktop {
        display: none;
      }

      &-container {
        margin-bottom: t-to-rem(-143);
        padding-bottom: 163%;
      }
    }
  }

  @include t-media-query($s-small-up) {
    &__header {
      margin-bottom: 7rem;

      &-inner {
        @include t-gutters(true);
        max-width: unset;
        padding-top: 15%;
        padding-bottom: 0;
        display: flex;
        justify-content: space-between;
      }

      &-left-container {
        margin-right: 10%;
        padding-top: 5rem;
      }

      &-heading {
        text-align: left;

        &:before {
          left: -238%;
        }

        &:after {
          right: -258%;
        }
      }
    }

    &__pre-title {
      margin-bottom: t-to-rem(40);
    }

    &__text {
      max-width: 48ch;
    }

    &__image {
      &.is-mobile {
        display: none;
      }

      &.is-desktop {
        display: block;
      }

      &-container {
        width: 56%;
        padding-bottom: 56%;
        margin-bottom: -15%;
      }
    }
  }
  
  @include t-media-query($s-medium-up) {
    &__image-container {
      padding-bottom: 45%;    
    }
  }
  @include t-media-query($s-large-up) {

    &__usps {
      display: flex;
      @include t-gutters(true);
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__header {
      &-inner {
        padding-top: 11.2%;
        padding-bottom: 2.3125rem;
      }

      &-left-container {
        padding-top: 5.25rem;
      }
    }

    &__pre-title {
      margin-bottom: 4rem;
    }

    &__image-container {
      width: 59%;
      padding-bottom: 44%;
      margin-bottom: -16%;
    }
  }
}